    static WxLogin(data){ //  
        let re = encodeURIComponent(data.redirectUrl);
        let info = {
            url:`https://open.weixin.qq.com/connect/oauth2/authorize?appid=${AllUrl.appid}&redirect_uri=${re}&response_type=code&scope=${data.type}&state=STATE#wechat_redirect`,
        }
        return info.url;
    } 
    static async WxToken(){
        let info = {
            url:`https://qyapi.weixin.qq.com/cgi-bin/gettoken?corpid=${AllUrl.appid}&corpsecret=${AllUrl.secret}`,
            type:'get',
            data:{},
        }
        let data_ = await Ajax(info);
        return data_;
    }

    static async WxUser(token,code){
        let info = {
            url:`https://qyapi.weixin.qq.com/cgi-bin/user/getuserinfo?access_token=${token}&code=${code}`,
            type:'get',
            data:{},
        }
        let data_ = await Ajax(info);
        return data_;
    }


    static async WxUserInfo(token,userId){
        let info = {
            url:`https://qyapi.weixin.qq.com/cgi-bin/user/get?access_token=${token}&userid=${userId}`,
            type:'get',
            data:{},
        }
        let data_ = await Ajax(info);
        return data_;
    }

    
