<template>
    <div class="Exam_info">
        <h1 class="title">
            {{currentInfo.name}}
        </h1>
        <h2>
            
        </h2>
        <h3>
            {{testInfo.length}}{{currentInfo.total_grade}}{{currentInfo.duration_time}}
        </h3>
        <h3>
            <span v-for="(el,index) in questionType" :key="index">
                {{el}}{{questionInfo[index]}}
                <span v-if="index!=(questionType.length-1)">,</span>
            </span>
        </h3>
        <h3>
            :{{currentInfo.startTime}}-{{currentInfo.endTime}}
        </h3>
        <h3>
            :
            <img src='@/assets/img_6.png' v-if="currentInfo.status_==1" />
            <img src='@/assets/img_10.png' v-else-if="currentInfo.status_==2" />
            <img src='@/assets/img_52.png' v-else />
        </h3>
        <h3 v-html="currentInfo.explain">
        </h3>

        <button :class="['Start_btn',currentInfo.status_===3?'active':'']" @click="GoTest">
            {{currentInfo.status_===3?'':''}}
            {{currentInfo.status_===1?'':''}}
            {{currentInfo.status_===2?'':''}}
        </button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                imgList:[ // 1   2   3  4  5  6 
                    require('../../assets/img_6.png'),
                    require('../../assets/img_10.png'),
                    require('../../assets/img_5.png'),
                    require('../../assets/img_7.png'),
                    require('../../assets/img_9.png'),
                    require('../../assets/img_8.png'),
                ],
                testInfo:[],
                questionType:['','','',''],
                questionInfo:[0,0,0,0],
            }
        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.examInfo;
            },
        },
        created() {
            this.GetInfo();
        },
        methods: {
            GoTest(){
                if(this.currentInfo.status_ == 4 || this.currentInfo.status_ == 5 || this.currentInfo.status_ == 6){
                    this.$router.push({path:'/exam/result'});
                }else if(this.currentInfo.status_==3){
                    this.$router.push({path:'/exam/test'});
                }
            },
            async GetInfo(){
                let id = this.currentInfo.id;
                if(!!!id){
                    id = this.GetQueryString('id');
                }
                let info = {
                    paper_id:id,
                }
                let detail = await this.Ajax.ExamineDetail(info);
                if(detail.code == 403){
                    this.$message.warning('');
                    let url = this.AllUrl.loginUrl + `?type=${this.$route.meta.type}&id=${id}`;
                    window.location.href = url;
                    return false;
                }
                if(detail.code != 0){
                    this.$message.warning(detail.message);
                    return false;
                }
                this.questionInfo = [0,0,0,0];
                this.testInfo = detail.data.map(el=>{
                    this.questionInfo[el.item_type-1]++;
                    if(el.item_type == 1 || el.item_type == 2){
                        el.item_option = JSON.parse(el.item_option);
                        el.option = [];
                        for (const i in el.item_option) {
                            el.option.push(el.item_option[i]);
                        }
                        el.answer = [];
                    }else if(el.item_type == 3){
                        el.option = ['',''];
                        el.answer = [];
                    }else{
                        el.answer = '';
                    }
                    return el;
                });
                this.$store.commit('setValue',{key:'testInfo',value:this.testInfo});
            },
        },
    }
</script>

